export default {
	props: {
		// 数据列表
		list: {
			type: Array,
			default () {
				return []
			}
		},
		// 加载动画
		loading: {
			type: Boolean,
			default: false
		},
		init: {
			type: Boolean,
			default: false
		},
		align: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			mark: 0,
			newList: [],
			boxHeight: [],
			top: [],
			left: [],
			loadingTop: 0,
			display: false,
		}
	},
	watch: {
		// 数据
		list: function(newVal, oldVal) {
			this.mark = oldVal.length;
			if (newVal != oldVal) {
				this.newList = this.list;
				// this.display = false;
				this.$nextTick(function() {
					setTimeout(() => {
						this.display = true;
					}, 270)
					setTimeout(() => {
						this.waterFall();
					}, 300)
				})
			}
		},
		init: function(newVal, oldVal) {
			this.mark = 0;
			this.newList = [];
			this.boxHeight = [];
			this.top = [];
			this.left = [];
			this.loadingTop = 0;
			this.display = false;
			setTimeout(() => {
				this.display = true;
			}, 270)
		}
	},
	methods: {
		// 瀑布流定位
		waterFall() {
			const query = uni.createSelectorQuery().in(this);
			query.selectAll('.commodityItem').boundingClientRect(res => {
				// console.log(res)
				let len = this.newList.length;
				let height = 0;
				for (let i = this.mark; i < len; i++) {
					height = res[i].height;
					if (i < 2) {
						this.$set(this.newList[i], 'top', 0);
						this.$set(this.newList[i], 'left', i);
						this.boxHeight.push(height);
						this.top.push(0);
						this.left.push(i);
					} else {
						let minHeight = this.boxHeight[0];
						let index = 0;
						if (minHeight > this.boxHeight[1]) {
							minHeight = this.boxHeight[1];
							index = 1;
						}
						this.boxHeight[index] = minHeight + height + 5;
						this.top.push(minHeight + 5);
						this.left.push(index);
						this.$set(this.newList[i], 'top', minHeight + 5);
						this.$set(this.newList[i], 'left', index);
						this.loadingTop = this.boxHeight[index];
					}
				}
			}).exec();
		},
		// 选中
		choose(commodity_id) {
			this.$emit('click', commodity_id);
		}
	}
}
